}

.prod-prev svg, .prod-next svg {
	display: block;
	width: 30px;
	height: 30px;
	position: absolute;
	top: 50%;
	margin: -15px 50px 0;
	pointer-events: none;
	opacity: 0;
	visibility: hidden;
	will-change: transform, opacity, visibility;
	transition-property: transform, opacity, visibility;
	transition-duration: 0.2s;
}

