	content: "";
	display: block;
	position: absolute;
	top: 4px;
	left: 4px;
	background: url("data:image/png;base64,");
	background-size: 2px;
}

.developer__logo .no-touchevents a:hover {
	transform: translate(0, 1px);
	filter: blur(2px);
	opacity: 0.6;
}

.developer__logo::after {
	content: "";
	display: block;
	position: absolute;
	background: #cc373c url("data:image/png;base64,") no-repeat center center;
	background-size: auto 16px;
}

.no-touchevents a:hover .developer__logo {
	color: black;
}

.no-touchevents a:hover .developer__logo::after {
	background-color: #00ef85;
}

/* developer end */
/* === FOOTER end === */
/* shutter overlay */
.shutter-overlay {
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	width: 100%;
	background-color: black;
	visibility: hidden;
	opacity: 0;
	z-index: 10;
	transition-property: opacity, visibility;
	transition-duration: 0.2s;
	transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

.shutter-overlay.active {
	visibility: visible;
	opacity: 0.35;
}

@media (min-width: 992px) {
	.shutter-only-mob .shutter-overlay.active {
		display: none;
	}
}

/* shutter overlay end */
/* btn-to-top */
.btn-to-top {
	position: fixed;
	right: 0;
	top: 50%;
	width: 150px;
	font-family: "AkzidenzGroteskPro Light Cond", sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 2.4rem;
	line-height: 1;
	color: #5a534f;
	text-align: center;
	z-index: 10;
	will-change: background-color, opacity, visibility;
	opacity: 0;
	visibility: hidden;
	transition-property: background-color, opacity, visibility;
	transition-duration: 0.2s;
}

@media (max-width: 1279px) {
	.btn-to-top {
		width: 100px;
	}
}

@media (max-width: 991px) {
	.btn-to-top {
		display: none;
	}
}

.btn-to-top span {
	display: block;
}

.btn-to-top svg {
	transform: rotate(0.5turn);
	display: inline-block;
	vertical-align: top;
}

.btn-to-top path {
	fill: currentColor;
}

.btn-to-top.btn-to-top--show {
	opacity: 1;
	visibility: visible;
}

.no-touchevents .btn-to-top:hover {
	color: #c4161c;
}

/* btn-to-top end */
