@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed:not(.css-scroll-fixed) {
		@include scroll-blocked(); }

	&.shutter-only-mob.css-scroll-fixed {
		@include media('<tab-lg') {
			@include scroll-blocked(); } } }

body {
	position: relative;
	@include font-normal;
	@include font($font-size, $line-height);
	color: $text-color;
	height: 100%;
	min-width: 320px;
	background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

	// выделение текста
	*::selection {
		background-color: $base-color;
		color: white; } }

/* === LAYOUT === */
.max-wrap {
	max-width: #{$page-layout-width + nth($page-layout-space, 2)*2};

	@include media('<desk-sm') {
		max-width: none; } }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<desk-sm') {
		@include padding-horizontal(nth($page-layout-space--md, 2)); }

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--sm, 2)); }

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

