// лэяут страницы
.wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	min-height: 100%;
	padding-top: 86px;
	position: relative;
	background: url(../../img/bg-img.png) center;

	@include media('<tab-lg') {
		padding-top: 0; }

	.sep-page & {
		@include media('>desk-hd') {
			background-attachment: fixed; }

		&::before {
			content: "";
			display: none;
			width: 50%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			background-color: $bg-color;

			@include media('>=tab-lg') {
				display: block; } } } }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	position: relative;
	clear: both;
	z-index: 4;

	@include padding-vertical(75px, 50px);

	@include media('<tab-lg') {
		@include padding-vertical(50px, 40px); }

	.sep-page & {
		padding: 0; } }

