/* === HEADER === */
.header {
	width: 100%; // нужно для браузеров не поддерживающих position: sticky
	min-width: 320px; // нужно для браузеров не поддерживающих position: sticky
	margin: 0;
	background-color: #403b38;
	position: sticky;
	top: 0;
	z-index: 15;
	transition: {
		property: background-color;
		duration: $animation-speed; }

	.shutter-after-open & {
		@include media('<tab-lg') {
			background-color: $base-color-alt; } }

	.layout {
		@include media('<tab-lg') {
			padding: 0; } } }

.header__holder {
	position: relative;
	//display: flex
	//justify-content: space-between
	//align-items: center
	text-align: center;
	height: $header-height;
	padding: 0 nth($logo-size, 2) + 10;
	z-index: 5;

	@include media('<tab-lg') {
		padding: 0 $header-height; } }

/* logo */
.logo {
	position: absolute;
	left: 0;
	top: 50%;
	width: nth($logo-size, 2);
	margin: -#{round(nth($logo-size, 1)/2)} 0 0;
	text-indent: -9999px;
	text-align: left;
	overflow: hidden;
	z-index: 5;

	@include media('<tab-lg') {
		width: nth($logo-short-size, 2);
		margin: -#{round(nth($logo-short-size, 1)/2)} 0 0 -#{round(nth($logo-short-size, 2)/2)};
		left: 50%; }

	&::before, &::after {
		content: "";
		display: block;
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: opacity;
			duration: $animation-speed; } }

	&::before {
		@include ratio(nth($logo-size, 2), nth($logo-size, 1));
		background: url(../../img/logo.png) no-repeat;

		@include media('<tab-lg') {
			@include ratio(nth($logo-short-size, 2), nth($logo-short-size, 1));
			background: none; }

		.header-is-top & {
			opacity: 0; } }

	&::after {
		position: absolute;
		left: 50%;
		top: 50%;
		opacity: 0;
		@include size(nth($logo-short-size, 2), nth($logo-short-size, 1));
		background: url(../../img/logo-short.png) no-repeat;
		margin: -#{round(nth($logo-short-size, 1)/2-1)} 0 0 -#{round(nth($logo-short-size, 2)/2-1)};

		@include media('<tab-lg') {
			opacity: 1;
			margin: -#{round(nth($logo-short-size, 1)/2)} 0 0 -#{round(nth($logo-short-size, 2)/2)}; }

		.header-is-top & {
			opacity: 1; } }

	a, & > span {
		position: absolute;
		left: 0;
		top: 50%;
		width: 100%;
		height: 100%;
		display: block;
		z-index: 1;
		transform: translate(0, -50%);
		will-change: transform;

		.header-is-top & {
			height: nth($logo-short-size, 1); } } }

/* logo end */
/* === HEADER end === */

/* === NAVIGATION === */
.nav-wrap, .nav-footer {
	&::before {
		position: absolute;
		content: "";
		display: none;
		top: 0;
		left: 20px;
		right: 20px;
		border: solid #cfb9aa;
		border-width: 0;

		@include media('<tab-lg') {
			display: block; } } }

.nav-wrap {
	display: inline-block; // для ие
	vertical-align: top; // для ие

	@include media('<tab-lg') {
		display: flex;
		flex-direction: column;
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		height: calc(100% - #{$header-height});
		background-color: $base-color-alt;
		opacity: 0;
		visibility: hidden;
		transition: {
			property: opacity, visibility;
			duration: $animation-speed; } }

	&.active {
		@include media('<tab-lg');
		@include media('<tab-lg') {
			opacity: 1;
			visibility: visible; } }

	&::before {
		border-bottom-width: 1px; } }

.nav {
	position: relative;
	width: 100%;

	@include media('<tab-lg') {
		display: flex;
		flex: 1 1;
		justify-content: center;
		align-items: center; } }

.nav__list {
	@include clear;

	@include media('<tab-lg') {
		width: 100%;
		max-height: 100%;
		overflow-x: hidden;
		overflow-y: auto; }

	li {
		float: left;

		@include media('<tab-lg') {
			float: none;
			display: block; } }

	a {
		padding: 10px 30px;
		display: block;
		display: flex;
		align-items: center;
		height: $header-height;
		@include font(2.4rem, round-float(30/24, 2));
		color: #e7dad2;
		text-decoration: none;

		@include media('<desk-lg') {
			@include padding-horizontal(15px); }

		@include media('<desk-md') {
			font-size: 2rem; }

		@include media('<desk-sm') {
			@include padding-horizontal(8px);
			font-size: 1.8rem; }

		@include media('<tab-lg') {
			color: $text-color;
			@include font-medium;
			@include font(3.6rem, round-float(48/36, 2));
			padding: 9px 20px;
			text-align: center;
			justify-content: center;
			height: auto; } }

	// hover
	.no-touchevents & {
		a:hover {
			box-shadow: inset 0 -2px 0 $base-color;

			@include media('<tab-lg') {
				box-shadow: none; } } }

	// current
	li.current {
		a {
			color: white;
			background-color: #c4161c;

			@include media('<tab-lg') {
				background-color: transparent;
				color: $base-color; } } } }


.nav-footer {
	position: relative;
	display: none;

	@include media('<tab-lg') {
		display: flex;
		width: 100%;
		min-height: $header-height; }

	&::before {
		border-top-width: 1px; } }

/* === NAVIGATION end === */

