/* === FOOTER === */
.footer {
	//margin: 0 auto
	position: relative;
	clear: both;
	z-index: 3; }

.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: $footer-grid-space;
	border: solid #5a534f;
	border-width: 1px 0 0; }

/* footer bottom */
.footer-bottom {
	position: relative;
	// обрезать вытянутый маржинами по горизонтале дочерний элемент
	overflow: hidden; }

.footer-bottom__holder {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	@include margin-horizontal(-#{round(nth($grid-space, 2)/2)});
	margin-bottom: -#{round(nth($grid-space, 1)/2)}; }

.footer-bottom__col {
	text-align: center;
	@include padding-horizontal(round(nth($grid-space, 2)/2));
	margin-bottom: #{round(nth($grid-space, 1)/2)};
	// для браузеров не поддерживающих display: flex
	display: inline-block;
	vertical-align: top;
	width: calc(33.33%);

	@include media('<mob-lg') {
		min-width: 100%; }

	&:first-child {
		@include media('>=mob-lg') {
			text-align: left; } }

	&:last-child {
		@include media('>=mob-lg') {
			text-align: right; } }

	a {
		color: inherit;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;
	min-height: 25px;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	@include media('<mob-lg') {
		display: block; }

	i {
		@include media('<mob-lg') {
			display: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;
	color: white;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	.no-touchevents a:hover {
		transform: translate(0, 1px);
		filter: blur(2px);
		opacity: 0.6; }

	&::after {
		@include pseudo();
		background: #cc373c url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px; } }

// hover
.no-touchevents a:hover {
	.developer__logo {
		color: black;

		&::after {
			background-color: #00ef85; } } }

/* developer end */
/* === FOOTER end === */

/* shutter overlay */
.shutter-overlay {
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	width: 100%;
	background-color: black;
	visibility: hidden;
	opacity: 0;
	z-index: 10;
	transition: {
		property: opacity, visibility;
		duration: $animation-speed;
		timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

	&.active {
		visibility: visible;
		opacity: 0.35;

		.shutter-only-mob & {
			@include media('>=tab-lg') {
				display: none; } } } } // скрывать на десктопе, если shutter-only-mob

/* shutter overlay end */

/* btn-to-top */
.btn-to-top {
	$btn-tt-bg-color: $text-color;
	//==============
	position: fixed;
	right: 0;
	top: 50%;
	width: nth($page-layout-space, 2);
	@include font-light;
	@include font(2.4rem, round-float(24/24, 2));
	color: #5a534f;
	text-align: center;
	z-index: 10; // под хедером, но над контентом и футером
	$prop: background-color, opacity, visibility;
	will-change: $prop;
	opacity: 0;
	visibility: hidden;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	@include media('<desk-sm') {
		width: nth($page-layout-space--md, 2); }

	@include media('<tab-lg') {
		display: none; }

	span {
		display: block; }

	svg {
		transform: rotate(0.5turn);
		display: inline-block;
		vertical-align: top; }

	path {
		fill: currentColor; }

	&.btn-to-top--show {
		opacity: 1;
		visibility: visible; }

	.no-touchevents &:hover {
		color: $base-color; } }

/* btn-to-top end */
